@import 'base/patterns';
.footer{
    .container{
        display: flex;
        flex-direction: column;
        .footer__wrapper{
            display: flex;
            justify-content: space-between;
            .footer__block{
                max-width: 582px;
                width: 100%;
                padding: 4.7rem 5.3rem 4.6rem 5.8rem;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                .footer__title-questions{
                    @extend %title_style;
                    font-size: 3.4rem;
                    text-align: left;
                    color:#FFFFFF;
                    margin-bottom: 3rem;
                }
                .footer__title-contacts{
                    @extend %title_style;
                    font-size: 3.4rem;
                    text-align: left;
                    margin-bottom: 3rem;
                    color: #000000;
                }
                .footer__communication{
                    font-family: 'Ubuntu';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 1.6rem;
                    line-height: 120%;
                    margin-bottom: 1.4rem;
                }
                .footer__adress{
                    font-family: 'Ubuntu';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 3rem;
                    line-height: 120%;
                }
            }
            .questions{
                background: #F83718;
                color:#FFFFFF;
            }
            .contacts{
                color: #000000;
                font-family: 'Ubuntu';
                font-style: normal;
                font-weight: 400;
                font-size: 2rem;
                line-height: 150%;
            }
        }
     .authorship{
        padding: 6rem 0;
        font-family: 'Ubuntu';
        font-style: normal;
        font-weight: 400;
        font-size: 2rem;
        line-height: 150%;
        text-align: center;
        color: #959595;
     }   
    }
}