%title_style{
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 4rem;
    line-height: 120%;
    text-align: center;
    text-transform: uppercase;

    color: #000000;
}

%orange_text{
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 4rem;
    line-height: 120%;
    color: #F83718;
}

%link_btn{
    padding: 0.9rem 2.8rem;
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 120%;
    text-align: center;
    letter-spacing: .02em;
    text-transform: uppercase;
    background: #EE816C;
    color: #FFFFFF;
    transition: .5s;
    &:hover{
        background: #F83718;
    }
}

%title_blocks{
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 120%;
    text-transform: uppercase;
    color: #000000;
}