@import 'base/fonts';
.header{
    margin: 3.2rem 0;
    .header__nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header__list{
            display: flex;
            gap: 5rem;
            .header__list-link{
                font-family: 'Ubuntu';
                font-style: normal;
                font-weight: 500;
                font-size: 1.4rem;
                line-height: 130%;
                letter-spacing: 0.02em;
                text-transform: uppercase;
                color: #000000;
                &:hover{
                    color: #F83718;
                }
            }
        }
        .header__language{
            display: flex;
            gap: 0.2rem;
            a{
                color: #666666;
                font-family: 'Ubuntu';
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 16px;
                text-transform: uppercase;
                padding: 1.422rem 1.4rem 1.422rem 1.422rem;
                border-radius: 6.94444px;
            }
            .header__language-active{
                color: #000000;
                background: #FFFFFF;
                box-shadow: 1.38889px 1.38889px 5.55556px rgba(48, 48, 56, 0.12);
                border-radius: 12px;
            }
        }
    }
}