@import 'base/patterns';
.container{
    max-width: 1164px;
    width: 100%;
    margin: 0 auto;
}
.main{
    .it{
        padding:10.39rem 0rem 6.841rem ;
        background-color: #F83718;
        margin-bottom: 10rem;
        .container{
            display: grid;
            grid-template-areas: "it_body languages";
            grid-column-gap: 5.1rem;
            align-items: start;
            .it__body{
                grid-area: it_body;
                display: flex;
                flex-direction: column;
                color: rgb(255, 255, 255);
                font-family: 'Ubuntu';
                font-style: normal;
                padding-left: 1rem;
                .it__body-title{
                    font-weight: 700;
                    font-size: 5rem;
                    line-height: 120%;
                    text-transform: uppercase;
                    margin-bottom: 3rem;
                }
                .it__body-text{
                    font-weight: 400;
                    font-size: 1.6rem;
                    line-height: 150%;
                    margin-bottom:3rem ;
                }
            }
            .vecteezy_back-end{
                grid-area: languages;
                display: block;
                position: relative;
                z-index: 100;
                &::after{
                    content: '';
                    display: block;
                    width: 180.09px;
                    height: 167.4px;
                    background: url('../img/main_img/square_dots.svg');
                    position: absolute;
                    bottom: -4px;
                    left: -20px;
                    z-index: -1;
                }
                &::before{
                    content: '';
                    display: block;
                    width: 180.09px;
                    height: 167.4px;
                    background: url('../img/main_img/test.svg');
                    position: absolute;
                    top:70px;
                    right: -2px;
                    z-index: -1;
                }
            }
        }
    }
    .filmtoolz{
        margin-bottom: 2rem;
        .container{
            display: flex;
            flex-direction: column;
            gap: 3rem;
            align-items: center;
            .filmtoolz__title{
                @extend %title_style;
            }
            .orange_style{
                @extend %orange_text;
            }
            .filmtoolz__text{
                font-family: 'Ubuntu';
                font-style: normal;
                font-weight: 300;
                font-size: 1.6rem;
                line-height: 150%;
                text-align: center;
                color: #000000;
            }
            .filmtoolz__wrapper{
                // width: 100%;
                display: flex;
                margin: 0 auto;
                align-items: center;
                // justify-content: space-around;
                gap: 15.4rem;
                .filmtoolz__block{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .filmtoolz__block-img{
                        width: 100px;
                        margin-bottom: 2.2rem;
                    }
                    .filmtoolz__block-info{
                        font-family: 'Ubuntu';
                        font-style: normal;
                        font-weight: 300;
                        font-size: 2.6rem;
                        line-height: 150%;
                    }
                }
            }
        }
    }
    .module{
        background-color: #FFFFFF;
        margin-bottom: 13.3rem;
        .container{
            display: flex;
            flex-direction: column;
            gap: 5rem;
            *:nth-child(even){
                flex-direction: row-reverse;
            }
            .module__block  {
                display: flex;
                align-items: center;
                justify-content: space-around;
                position: relative;
                z-index: 100;
                .module__dots{
                    position: absolute;
                    bottom: 0;
                    z-index: -1;
                }
                .right{
                    right:39px;
                }
                .left{
                    left: 49px;
                }
                .module__body{
                    display: flex;
                    flex-direction: column;
                    gap: 3rem;
                    .module__image{
                        filter: drop-shadow(0px 2px 25px rgba(75, 77, 83, 0.25));
                        width: 100%
                    }
                    .module__block-title{
                        @extend %title_blocks;
                    }
                    .module__block-text{
                        max-width: 364.5px;
                        width: 100%;
                        font-family: 'Ubuntu';
                        font-style: normal;
                        font-weight: 300;
                        font-size: 1.6rem;
                        line-height: 120%;
                        color: #1B1925;
                    }
                    .module__block-link{
                        @extend %link_btn;
                        align-self: flex-start;    
                    }
                }
            }
        }
    }
    .casting{
        margin-bottom: 13.3rem;
        .container{
            display: flex;
            flex-direction: column;
            gap: 3rem;
            align-items: center;
            justify-content: center;
            width: 691px;
            .casting__title{
                @extend %title_style;
                font-size: 3.4rem;
            }
            .casting__text{
                font-family: 'Ubuntu';
                font-style: normal;
                font-weight: 300;
                font-size: 1.6rem;
                line-height: 150%;
                text-align: center;
                color: #000000;
                width: 551px;
            }
            .casting__link{
                @extend %link_btn;
                margin-bottom: 2.7rem;
            }
            .casting__wrapper{
                display: flex;
                gap: 10rem;
                .casting__block{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .casting__block-stastics{
                        @extend %orange_text;
                    }
                    .casting__block-info{
                        font-family: 'Ubuntu';
                        font-style: normal;
                        font-weight: 300;
                        font-size: 2.6rem;
                        line-height: 150%;
                        color: #000000;
                    }
                }
            }
            .casting__image{
                width: 100%;
            }
        }
    }
    .doctoolz{
        background-color: #FFFFFF;
        height: 423px;
        .doctoolz__wrapper{
            position: relative;
            .container{
                position: absolute;
                top: -20px;
                left:calc(50% - 5px);
                transform:translateX(-50%);
                flex-direction: column; 
                z-index: 1;;
                .doctoolz__title{
                    @extend %title_style;
                    margin-bottom: 4.7rem;
                }
                .doctoolz__block{
                    display: flex;
                    align-items: center;
                    background-color: #FFFFFF;
                    border: 4px solid #F9F9F9;
                    position: relative;
                    top: 0;
                    gap: 4.48rem;
                    &::before{
                        content: '';
                        display: block;
                        width: 167.14px;
                        height: 167.4px;
                        background-image: url('../img/filmtoolz/square_dots.svg');
                        position: absolute;
                        top: -28px;
                        right: -24px;
                        z-index: -1;
                    }
                    &::after{
                        content: '';
                        display: block;
                        width: 167.14px;
                        height: 167.4px;
                        background-image: url('../img/main_img/square_dots.svg');
                        position: absolute;
                        bottom: -28px;
                        left: -24px;
                        z-index: -1;
                    }
                    .doctoolz__block-image{
                        filter: drop-shadow(0px 2px 25px rgba(75, 77, 83, 0.25));
                    }
                    .doctoolz__body{
                        display: flex;
                        flex-direction: column;
                        gap:3rem;
                        .doctoolz__body-title{
                            @extend %title_blocks;
                            max-width: 424px;
                            width: 100%;
                        }
                        .doctoolz__body-text{
                            max-width: 364px;
                            width: 100%;
                            font-family: 'Ubuntu';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 1.6rem;
                            line-height: 120%;
                        }
                        .doctoolz__body-link{
                            @extend %link_btn;
                            align-self: flex-start;
                        }
                    }
                }
            }
        }
    }
    .function{
        background: #01AFEB;
        height: 635px;
        position: relative;
        margin-bottom: 15rem;
        .wrapper__function{
            position: absolute;
            top: 259px;
            left: 50%;
            transform:translateX(-50%);
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 2rem;
            .function__block{
                display: flex;
                flex-direction: column;
                align-items: center;    
                max-width: 276px;
                width: 100%;

                .function__icon{
                    width: 100px;
                    margin-bottom: 2.2rem;
                }
                .function__title{
                    font-family: 'Ubuntu';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 2.4rem;
                    line-height: 120%;
                    text-transform: uppercase;
                    color: #FFFFFF;
                    margin-bottom: 0.6rem;
                }
                .function__text{
                    font-family: 'Ubuntu';
                    font-style: normal;
                    width: 202px;
                    font-weight: 400;
                    font-size: 1.6rem;
                    line-height: 150%;
                    color: #FFFFFF;
                    text-align: center;
                }
            }
        }
    }
    .development{
        margin-bottom: 15rem;
        .container{
            display: grid;
            grid-template-areas: 'maindev TV commerce'
            'maindev automation mobile';
            grid-column-gap: .9rem;
            // justify-content: start    ;
            align-items:center;
            .development__main{
                display: flex;
                flex-direction: column;
                align-items: center;
                grid-area: maindev;
                .development__main-title{
                    @extend %title_style;
                    max-width: 301px;
                    width: 100%;
                    text-align: center;
                    margin-bottom: 2rem;
                }
                .development__main-text{
                    font-family: 'Ubuntu';
                    font-style: normal;
                    font-weight: 300;
                    font-size: 1.6rem;
                    line-height: 120%;
                    text-align: center;
                    color: #1B1925;
                    max-width: 266px;
                    width: 100%;
                    margin-bottom: 4.4rem;
                }
                .development__main-link{
                    @extend %link_btn;
                }
            }
            .development__block{
                display: flex;
                flex-direction: column;
                gap: 1.4rem;
                align-items: center;
                min-width: 402px;
                width: 100%;
                min-height: 246px;
                height: 100%;
                &:hover{
                    background: #F83718;
                    .development__block-icon{
                        display: none;
                    }
                    .development__block-title{
                        display: none;
                    }
                    .development__block-text{
                        animation: opac 2s;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        max-width: 346px;
                        width: 100%;
                    }
                }
                .development__block-icon{
                    width: 100px;
                }
                .development__block-title{
                    @extend %title_blocks;
                    text-align: center;
                }
                .development__block-text{
                    display: none;
                    text-align: center;
                    height: 246px;
                    padding: 0 2.8rem;
                    font-family: 'Ubuntu';
                    font-style: normal;
                    font-weight: 300;
                    font-size: 1.6rem;
                    line-height: 120%;    
                    color: #FFFFFF;
                }
            }
            .tv{
                grid-area: TV;
            }
            .commerce{
                grid-area: commerce;
            }
            .automation{
                grid-area: automation;
            }
            .mobile{
                grid-area: mobile;
            }
        }
    }
}
@keyframes opac {
    from{
        opacity: .7;
    }
    to{
        opacity: 1;
    }
}